
import { defineComponent } from 'vue';
import Loading from '@/components/Loading.vue';
import Menu from '@/components/Menu.vue';
import Todo from '@/components/Todo.vue';
import axios from 'axios';
import baseUrl from '../config/baseUrl';
import TodoType from '../config/todoType';

export default defineComponent({
  name: 'Home',

  data: () => ({
    todoInput: '',
    dataLoading: true,
    dataTodos: [] as TodoType[],
    sortByNameStatus: false,
    showCompletedTasks: true,
  }),

  components: {
    Loading,
    Todo,
    Menu,
  },

  mounted() {
    this.getData();
  },

  methods: {
    getData() {
      axios
        .get(baseUrl)
        .then(({ data }) => {
          this.dataTodos = data;
        })
        .catch((error) => {
          alert(
            `${error.response.status} (${error.response.statusText}), try again`
          );
        })
        .finally(() => (this.dataLoading = false));
    },

    createTodo() {
      if (!this.todoInput.trim()) {
        alert('Please add a task');
      } else {
        this.dataLoading = true;
        axios
          .post(baseUrl, {
            name: this.todoInput,
          })
          .then(() => {
            this.getData();
            this.sortByNameStatus = false;
          })
          .catch((error) => {
            alert(
              `${error.response.status} (${error.response.statusText}), try again`
            );
          })
          .finally(() => {
            this.dataLoading = false;
            this.todoInput = '';
          });
      }
    },

    sortByName() {
      this.sortByNameStatus = !this.sortByNameStatus;
      this.dataLoading = true;
      if (!this.sortByNameStatus) {
        this.getData();
        return;
      } else {
        axios
          .get(`${baseUrl}?sortBy=name&order=desc`)
          .then(({ data }) => {
            this.dataTodos = data.reverse();
          })
          .catch((error) => {
            alert(
              `${error.response.status} (${error.response.statusText}), try again`
            );
          })
          .finally(() => {
            this.dataLoading = false;
            this.todoInput = '';
          });
      }
    },

    toggleCompleteTodo(id: string, completed: boolean) {
      this.dataLoading = true;
      axios
        .put(`${baseUrl}/${id}`, {
          done: !completed,
        })
        .then(() => {
          this.getData();
        })
        .catch((error) => {
          alert(
            `${error.response.status} (${error.response.statusText}), try again`
          );
        })
        .finally(() => {
          this.dataLoading = false;
        });
    },

    deleteTodo(id: string) {
      this.dataLoading = true;
      axios
        .delete(`${baseUrl}/${id}`)
        .then(() => {
          this.getData();
        })
        .catch((error) => {
          alert(
            `${error.response.status} (${error.response.statusText}), try again`
          );
        })
        .finally(() => {
          this.dataLoading = false;
        });
    },

    toggleCompletedTodos() {
      this.showCompletedTasks = !this.showCompletedTasks;
    },
  },

  computed: {
    filteredTodos() {
      if (!this.showCompletedTasks) {
        return this.dataTodos.filter((todo) => {
          return !todo.done;
        });
      }
      return this.dataTodos;
    },
  },
});
